	module.exports = {"data":{"markdownRemark":{"html":"<h1>云南大学IT资源申请表</h1>\n<p>信息技术中心为学校各部门提供各类IT资源，包括域名、虚拟机、固定IP地址等。为了更好地管理各类IT资源，IT资源申请需按照以下流程进行：</p>\n<ol>\n<li>申请单位前往信息技术中心网站<a href=\"http://www.itc.ynu.edu.cn/docs/2016-12/20161220150328975034.docx\">下载《云南大学IT资源申请表》</a>，并按要求填写表格；</li>\n<li>《申请表》填写完成后，送到信息技术中心运维中心办公室（礼贤楼2201），由IT资源申请业务负责人进行初审，并给出初审意见；</li>\n<li>如果需要申请开通网站，申请单位还需将《申请表》连同其他材料(《保密责任协议》等）送交校保密委审批。</li>\n<li>申请单位将完成第2，3步审批的《申请表》交给运维中心工作人员，等待学校信息化领导小组最后审批，以及信息技术中心工作人员完成IT资源的开通。</li>\n<li>运维中心工作人员将审批之后的《申请表》送交信息技术中心相应的管理人员进行IT资源的开通；</li>\n<li>IT资源开通后，运维中心工作人员通过微信企业号告知申请单位技术负责人开通情况。涉及密码等敏感信息时，应当使用保密消息发送。</li>\n<li>运维中心工作人员将《申请表》存档保存。</li>\n</ol>\n<h2>相关附件</h2>\n<ul>\n<li><a href=\"http://www.itc.ynu.edu.cn/system/_content/download.jsp?urltype=news.DownloadAttachUrl&#x26;owner=1355056356&#x26;wbfileid=2127399\">示例-申请在站群系统上开通网站v2.3.pdf</a></li>\n<li><a href=\"http://www.itc.ynu.edu.cn/system/_content/download.jsp?urltype=news.DownloadAttachUrl&#x26;owner=1355056356&#x26;wbfileid=2127398\">示例-申请虚拟机并开通网站v2.3.pdf</a></li>\n<li><a href=\"http://www.itc.ynu.edu.cn/system/_content/download.jsp?urltype=news.DownloadAttachUrl&#x26;owner=1355056356&#x26;wbfileid=2127397\">示例-申请打印机固定ipv2.3.pdf</a></li>\n<li><a href=\"http://www.itc.ynu.edu.cn/system/_content/download.jsp?urltype=news.DownloadAttachUrl&#x26;owner=1355056356&#x26;wbfileid=2218737\">云南大学it资源申请表v2.3.docx</a></li>\n<li><a href=\"http://www.itc.ynu.edu.cn/system/_content/download.jsp?urltype=news.DownloadAttachUrl&#x26;owner=1355056356&#x26;wbfileid=2127401\">网站申请表附加内容（保密责任协议）.docx</a></li>\n</ul>","fields":{"slug":"yun-nan-da-xue-itzi-yuan-shen-qing-biao.html"},"frontmatter":{"title":"云南大学IT资源申请表","date":"December 6th 2017","tags":["申请流程"]}}},"pathContext":{"slug":"yun-nan-da-xue-itzi-yuan-shen-qing-biao.html","prev":{"fields":{"slug":"yun-nan-da-xue-vpnshi-yong-zhi-dao.html"},"frontmatter":{"title":"云南大学VPN使用指导","date":"2017-12-06T10:16:10.000Z","tags":["VPN","使用指导"]}},"next":{"fields":{"slug":"ms17-010bu-ding-xia-zai.html"},"frontmatter":{"title":"MS17-010补丁下载","date":"2017-12-06T10:16:10.000Z","tags":["勒索病毒补丁"]}}}}

